  .company-methods__image-wrap {
    padding-bottom: 3rem;
  }
}
.company-methods__bg-text {
  position: absolute;
  z-index: -1;
  top: 20rem;
  left: 3rem;
  font-size: 14.6rem;
  font-weight: 700;
  text-transform: uppercase;
  white-space: nowrap;
  line-height: 170%;
  opacity: 0.1;
}
@media (max-width: 48em) {
  .company-methods__bg-text {
    top: 22rem;
    left: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 8rem;
    transform: translateX(-50%);
  }
  .company-methods__bg-text span {
    font-size: 21.8rem;
  }
}
@media (max-width: 48em) {
  .company-methods__link {
    transform: none;
  }
}

.hero-portfolio {
  position: relative;
  margin-bottom: 18rem;
}
.hero-portfolio__container {
  position: relative;
  min-width: 0;
}
.hero-portfolio__container::before {
  z-index: 2;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.2);
}
.scroll-trigger .hero-portfolio__container::before {
  transform: scaleY(var(--scaleY));
}
@media (max-width: 48em) {
  .hero-portfolio__container {
    padding: 0;
  }
  .hero-portfolio__container::before {
    display: none;
  }
}
@media (min-width: 48em) {
  .hero-portfolio__container {
    grid-column: 2/3;
  }
}
.hero-portfolio__carousel {
  position: relative;
}
@media (min-width: 48em) {
  .hero-portfolio__slider.swiper {
    overflow: visible;
  }
}
@media (max-width: 48em) {
  .hero-portfolio__slider {
    margin-bottom: 10rem;
  }
}
.hero-portfolio__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
@media (min-width: 48em) {
  .hero-portfolio__content {
    position: absolute;
    top: 12rem;
    left: 3rem;
    width: calc(72.7% - 3rem);
    max-height: calc(100% - 12rem);
  }
}
@media (max-width: 48em) {
  .hero-portfolio__content {
    padding: 0 3.2rem;
  }
}
.hero-portfolio__group {
  margin-bottom: 12rem;
  display: flex;
  align-items: center;
  column-gap: 3rem;
}
.hero-portfolio__group::after {
  content: "";
  width: 90.4rem;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.5);
}
@media (max-width: 48em) {
  .hero-portfolio__group {
    position: absolute;
    top: 77.2rem;
    left: 3.2rem;
    column-gap: 2rem;
  }
  .hero-portfolio__group::after {
    width: 19.4rem;
  }
}
.hero-portfolio__group-txt {
  white-space: nowrap;
}
.hero-portfolio__group-txt:first-child {
  order: -1;
}
.hero-portfolio__group-txt:last-child {
  order: 1;
}
@media (max-width: 48em) {
  .hero-portfolio__group-txt {
    font-size: 2.8rem;
  }
}
.hero-portfolio__text-wrap {
  display: flex;
  flex-direction: column;
}
@media (min-width: 48em) {
  .hero-portfolio__text-wrap {
    margin-bottom: 10.2rem;
  }
}
.hero-portfolio__title {
  margin-bottom: 3.2rem;
  display: flex;
  flex-direction: column;
}
@media (max-width: 48em) {
  .hero-portfolio__title {
    margin-bottom: 5rem;
    flex-direction: row;
    column-gap: 2rem;
  }
}
@media (min-width: 48em) {
  .hero-portfolio__text {
    max-width: 36.4rem;
    color: #b3b3b3;
  }
}
.hero-portfolio__slider-control {
  display: flex;
  flex-direction: column;
}
@media (max-width: 48em) {
  .hero-portfolio__slider-control {
    position: absolute;
    bottom: 0;
    right: 3.2rem;
    flex-direction: column-reverse;
  }
}
.hero-portfolio__slider-nav {
  display: flex;
  flex-direction: column;
  row-gap: 1.6rem;
}
@media (min-width: 48em) {
  .hero-portfolio__slider-nav {
    margin-bottom: 10.2rem;
  }
}
@media (max-width: 48em) {
  .hero-portfolio__slider-nav {
    flex-direction: column-reverse;
    align-items: flex-end;
  }
}
@media (max-width: 48em) {
  .hero-portfolio__slider-fraction {
    margin-bottom: 8rem;
    font-size: 3.6rem;
  }
}
.hero-portfolio__thumbs-carousel {
  position: absolute;
  z-index: 5;
  bottom: 10rem;
  right: 0;
  overflow: hidden;
}
.hero-portfolio__thumbs {
  width: 68.2rem;
  height: 23.3rem;
}
.hero-portfolio__thumbs.swiper {
  overflow: visible;
}
.hero-portfolio__thumb {
  width: 23.5rem;
  cursor: pointer;
}
.hero-portfolio__thumb-image-wrap {
  height: 100%;
}
.hero-portfolio__thumb-image {
  height: 100%;
  object-fit: cover;
}

.slide-hero-portfolio {
  display: grid;
  width: 100%;
}
@media (min-width: 48em) {
  .slide-hero-portfolio {
    grid-template-columns: 8fr 3fr;
    min-height: 100rem;
  }
}
.slide-hero-portfolio__image-wrap {
  position: relative;
}
.slide-hero-portfolio__image-wrap::after {
  content: "";
  position: absolute;
  top: 0;
  left: -46.4rem;
  height: 100%;
  width: calc(46.4rem + 100%);
  background: linear-gradient(70deg, rgba(46, 46, 46, 0.3) 20.45%, rgba(0, 0, 0, 0) 63.6%), linear-gradient(0deg, rgba(70, 74, 80, 0.2) 0%, rgba(70, 74, 80, 0.4) 100%), linear-gradient(0deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (max-width: 48em) {
  .slide-hero-portfolio__image-wrap {
    margin-bottom: 3.2rem;
    height: 84rem;
  }
}
.slide-hero-portfolio__image {
  height: 100%;
  object-fit: cover;
}
@media (min-width: 48em) {
  .slide-hero-portfolio__image {
    position: absolute;
    top: 0;
    left: -46.4rem;
    width: calc(46.4rem + 100%);
  }
}
.slide-hero-portfolio__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
@media (min-width: 48em) {
  .slide-hero-portfolio__content {
    padding-top: 26.3rem;
    padding-bottom: 43.3rem;
    padding-left: 14.9rem;
    width: calc(100% + 192rem - 145.6rem);
  }
}
@media (max-width: 48em) {
  .slide-hero-portfolio__content {
    padding-left: 3.2rem;
    max-width: 58rem;
  }
}
.slide-hero-portfolio__title-wrap {
  margin-bottom: 3.2rem;
  display: flex;
  flex-direction: column;
  row-gap: 0.8rem;
}
@media (max-width: 48em) {
  .slide-hero-portfolio__title-wrap {
    margin-bottom: 2.4rem;
    flex-direction: column-reverse;
  }
}
.slide-hero-portfolio__subtitle {
  color: #b2b2b2;
}
.slide-hero-portfolio__text {
  margin-bottom: 3.2rem;
}
@media (max-width: 48em) {
  .slide-hero-portfolio__text {
    margin-bottom: 2.4rem;
  }
}
.slide-hero-portfolio__square {
  margin-bottom: 4.8rem;
  display: flex;
  flex-direction: column;
  row-gap: 4.8rem;
}
.slide-hero-portfolio__square::after {
  content: "";
  width: 4rem;
  height: 2px;
  background-color: #ffffff;
}
@media (max-width: 48em) {
  .slide-hero-portfolio__square {
    margin-bottom: 4rem;
    row-gap: 4rem;
  }
  .slide-hero-portfolio__square::after {
    width: 8rem;
  }
}
.slide-hero-portfolio__link {
  transform: none;
}

.lock body {
  overflow: hidden;
  touch-action: none;
}
.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  width: 192rem;
  margin: 0 auto;
  overflow: hidden;
}
.wrapper::before {
  z-index: 5;
  left: 0;
  top: 0;
}
.wrapper::after {
  z-index: 5;
  right: 0;
  top: 0;
}
@media (max-width: 120em) {
  .wrapper::before, .wrapper::after {
    display: none;
  }
}
@media (max-width: 48em) {
  .wrapper {
    width: 100%;
  }
}
.wrapper > main {
  flex: 1 1 auto;
}
.wrapper > * {
  min-width: 0;
